.blogSection {
    padding: 50px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .content {
    display: flex;
    max-width: 1200px;
    align-items: center;
    gap: 40px;
  }
  
  .imageBlock {
    flex: 1;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    width: 100%;
    max-width: 600px;
  }
  
  .purpleShape {
    position: absolute;
    background-color: #592EA9;
    width: 60px;
    height: 60px;
    bottom: -30px;
    left: 90px;
    z-index: 1;
    border-radius: 50%;
  }
  
  .textBlock {
    flex: 1;
  }
  
  .title {
    font-size: 36px;
    line-height: 48px;
    letter-spacing: -2px;
    color: #232536;
    font-weight: bold;
    margin-bottom: 10px;
    font-family: "Sen", serif;
  
  }
  
  .subtitle {
    color: #232536;
    margin-bottom: 15px;
    font-family: "Sen", serif;
    font-weight: bold;
    font-size: 24px;
    line-height: 32px;
  }
  
  .description {
    font-size: 16px;
    color: #232536;
    line-height: 28px;
    font-family: "Inter", serif;
    font-weight: 400;
  }
  