.container {
    color: white;
    display: flex;
    align-items: center;
}

.content {
    margin-left: 80px;
    width: 70%;
}

.span {
    font-size: 16px;
    letter-spacing: 3px;
}

.span_span {
    font-size: 16px;
    letter-spacing: 3px;
}

.title {
    margin-top: 44px;
    font-size: 56px;
}

.desc_span {
    margin-top: 24px;
    font-size: 16px;
}

.desc_author {
    color: rgba(255, 208, 80, 1);
}

.brief_desc {
    margin-top: 16px;
    font-size: 16px;
}

.button {
    margin-top: 48px;
}

/* Styles for screens 768px to 992px */
@media (min-width: 768px) and (max-width: 992px) {
    .content {
        margin-left: 40px;
        width: 80%;
    }
    
    .title {
        font-size: 48px;
    }
    
    .desc_span, .brief_desc {
        font-size: 14px;
    }
    
    .button {
        margin-top: 32px;
    }
}

/* Styles for screens 992px to 1200px */
@media (min-width: 992px) and (max-width: 1200px) {
    .content {
        margin-left: 60px;
        width: 75%;
    }
    
    .title {
        font-size: 52px;
    }
    
    .desc_span, .brief_desc {
        font-size: 15px;
    }
    
    .button {
        margin-top: 40px;
    }
}

/* Styles for screens larger than 1200px */
@media (min-width: 1200px) {
    .content {
        margin-left: 80px;
        width: 70%;
    }
    
    .title {
        font-size: 56px;
    }
    
    .desc_span, .brief_desc {
        font-size: 16px;
    }
    
    .button {
        margin-top: 48px;
    }
}
