.feat_post{
    height: 519px;
    background-color: Lavender;
    display: flex;
    flex-direction: row;
    padding: 80px;
    justify-content: space-between;
}

.feat_post_text_h3{
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 3px;
    margin-bottom: 24px;
}

.feat_post_text_h2{
    font-size: 36px;
    line-height: 48px;
    letter-spacing: -2px;
    margin-bottom: 24px;
    text-decoration: none;
    color: black;
    display: inline-block;
}

.feat_post_text_p{
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 16px;
}

.feat_post_desc{
    font-size: 16px;
    line-height: 28px;
    color: rgba(109, 110, 118, 1);
    margin-bottom: 16px;
}

.feat_post_author{
    color: rgba(89, 46, 169, 1);
}

.category_h2{
    margin-top: 128px;
    font-size: 36px;
    line-height: 48px;
    letter-spacing: -2px;
    margin-bottom: 48px;
}

.all_posts{
    padding: 0 80px 0 80px;
}

.all_posts_h1{
    padding-bottom: 32px;
    font-size: 48px;
    line-height: 64px;
    margin-top: 48px;
}

.all_posts_section{
    display: flex;
    flex-direction: column;
    padding-top: 48px;
    border-top: 1px solid rgba(109, 110, 118, 0.2);
}

.post{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 48px;
}

.post_text{
    padding: 24px;
}

.purple{
    color: rgba(89, 46, 169, 1);
}

.prev{
    font-size: 24px;
    line-height: 32px;
    color: rgba(109, 110, 118, 1);
}

.next{
    font-size: 28px;
    line-height: 40px;
    letter-spacing: -1px;
    margin-left: 16px;
}

.switcher{
    text-align: center;
}

/* Default styles are applied to smaller screens, typically less than 768px */

/* Styles for screens from 768px to 992px */
@media (min-width: 768px) and (max-width: 992px) {
    .feat_post {
        flex-direction: column;
        height: auto;
        padding: 40px;
    }

    .feat_post img {
        width: 100%;
        height: auto;
        margin-top: 20px;
    }

    .feat_post_text {
        text-align: center;
    }

    .all_posts {
        padding: 0 40px;
    }

    .all_posts_section {
        flex-direction: column;
    }

    .post {
        flex-direction: column;
        align-items: flex-start;
    }

    .post img {
        width: 100%;
        height: auto;
    }

    .post_text {
        padding: 16px 0;
    }

    .switcher {
        margin-top: 20px;
        display: flex;
        justify-content: center;
    }
}

/* Styles for screens from 992px to 1200px */
@media (min-width: 992px) and (max-width: 1200px) {
    .feat_post {
        padding: 60px;
    }

    .feat_post_text{
        margin-right: 32px;
    }

    .all_posts {
        padding: 0 60px;
    }

    .post {
        margin-bottom: 32px;
    }

    .switcher span {
        font-size: 20px;
    }
}

/* Styles for screens wider than 1200px */
@media (min-width: 1200px) {
    .feat_post {
        padding: 80px;
        flex-direction: row;
    }

    .feat_post img {
        width: 50%;
    }

    .all_posts {
        padding: 0 80px;
    }

    .post {
        flex-direction: row;
        margin-bottom: 48px;
    }

    .post img {
        width: 40%;
    }

    .post_text {
        padding: 24px;
    }

    .switcher {
        margin-top: 40px;
        text-align: center;
    }
}
