.teamSection {
  padding: 50px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content {
  display: flex;
  max-width: 1200px;
  align-items: center;
  gap: 40px;
}

.textBlock {
  flex: 1;
}

.title {
  font-size: 36px;
  line-height: 48px;
  letter-spacing: -2px;
  color: #232536;
  font-weight: bold;
  margin-bottom: 10px;
  font-family: "Sen", serif;

}

.subtitle {
  color: #232536;
  margin-bottom: 15px;
  font-family: "Sen", serif;
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
}

.description {
  font-size: 16px;
  color: #232536;
  line-height: 28px;
  font-family: "Inter", serif;
  font-weight: 400;
}

.imageBlock {
  flex: 1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.yellowShape {
  position: absolute;
  background-color: #FFD050;
  width: 80px;
  height: 95px;
  top: 90px;
  left: -20px;
  border-radius: 25px 0px 0px 0px;
}

.image {
  width: 100%;
  max-width: 500px;
}