.container {
  max-width: 768px;
  margin: 0 auto;
  padding: 16px;
  font-family: Arial, sans-serif;
  margin-bottom: 96px;
}

.header {
  text-align: center;
  margin-bottom: 24px;
  font-size: 14px;
  padding-top: 100px;
}

.header h2 {
  margin-bottom: 8px;
}

.header h1 {
  margin-bottom: 20px;
}

.header p {
  color: #6D6E76;
  padding-bottom: 40px;
}

.info {
  display: flex;
  background-color: #592EA9;
  color: white;
  padding: 30px;
  height: 240px;
  margin-bottom: 50px;
}

.workingHours,
.contactDetails {
  flex: 1;
  margin-right: 16px;
}

.info h4 {
  margin-bottom: 15px;
  font-size: 12px;
  padding-bottom: 15px;
  border-bottom: 1px solid;
}

.info p {
  margin: 8px 0;
  font-size: 16px;
}

.input {
  width: 100%;
  padding: 20px;
  margin-bottom: 12px;
  border: 1px solid #ccc;
  font-size: 14px;
}

.textarea {
  width: 100%;
  padding: 21px;
  border: 1px solid #ccc;
  height: 125px;
  font-size: 18px;
  margin-bottom: 12px;
}

.button {
  background-color: #FFD050;
  padding: 20px;
  border: none;
  font-size: 16px;
  cursor: pointer;
  width: 100%;
}

.button:hover {
  background-color: #e6ac00;
}

.h2 {
  font-family: "Inter", serif;
  font-weight: 900;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 3px;
}

.h1 {
  font-family: "Sen", serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 64px;
  letter-spacing: -2px;
}

.p {
  font-family: "Inter", serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
}

.p2 {
  font-family: "Sen", serif;
  font-weight: 700;
}

.error {
  border: 2px solid red;
  background-color: #ffe6e6;
}

.errorMessage {
  color: red;
  font-size: 14px;
  margin-bottom: 10px;
}
