.first_img{
	width: calc(100% - 160px);
}

.first_div{
	display: flex;
	flex-direction: row;
	align-items: center;
	width: calc(100% - 160px);
	margin-top: 96px;
	margin-bottom: 48px;
}

.first_div_p{
	margin-bottom: 24px;
}

.first_div_h1{
	font-size: 48px;
	line-height: 64px;
	letter-spacing: -2px;
}

.first_div_desc{
	margin-left: 64px;
	font-size: 16px;
	line-height: 28px;
}

.first_wrapper{
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
}

.text{
	background-color: rgba(244, 240, 248, 1);
	padding: 96px 96px 64px 96px;
	width: calc(100% - 160px);
	display: flex;
	flex-direction: row;
}

.text_div2{
	margin-left: 48px;
}

.text_p{
	font-size: 16px;
	line-height: 20px;
	letter-spacing: 3px;
	margin-bottom: 32px;
}

.text_h3{
	font-size: 28px;
	line-height: 40px;
	letter-spacing: -1px;
	margin-bottom: 24px;
}

.text_p2{
	font-size: 16px;
	line-height: 28px;
	color: rgba(109, 110, 118, 1);
}

.stats {
	display: flex;
	justify-content: space-between;
	position: absolute;
	bottom: 40%;
	left: 10%;
	background-color: #FFD050;
	padding: 40px 45px;
	width: 50%;
}
  
  .statNumber {
	font-size: 35px;
	margin-bottom: 5px;
	font-family: "Sen", serif;
	font-size: 56px;
	line-height: 64px;
	letter-spacing: -2px;
	font-weight: bold;
  }
  
  .statText {
	font-size: 16px;
	line-height: 28px;
	font-family: "Inter", serif;
  }

  /* Медиазапросы для экранов от 768px до 992px */
@media (min-width: 768px) and (max-width: 992px) {
	.first_div {
		flex-direction: column;
		text-align: center;
		width: 90%;
		margin-top: 48px;
		margin-bottom: 32px;
	}

	.first_div_desc {
		margin-left: 0;
		margin-top: 16px;
	}

	.stats {
		position: relative;
		bottom: auto;
		left: auto;
		width: 90%;
		flex-direction: column;
		padding: 20px;
		text-align: center;
	}

	.statItem {
		margin-bottom: 16px;
	}

	.text {
		flex-direction: column;
		padding: 48px 24px;
		width: 90%;
	}

	.text_div2 {
		margin-left: 0;
		margin-top: 32px;
	}

	.first_img {
		width: 100%;
		margin-top: 24px;
	}
}

/* Медиазапросы для экранов от 992px до 1200px */
@media (min-width: 992px) and (max-width: 1200px) {
	.first_div {
		width: 85%;
		margin-top: 72px;
		margin-bottom: 40px;
	}

	.first_div_desc {
		margin-left: 32px;
	}

	.stats {
		width: 70%;
		padding: 30px;
	}

	.text {
		width: 85%;
		padding: 64px 48px;
	}

	.text_div2 {
		margin-left: 24px;
	}
}

/* Медиазапросы для экранов более 1200px */
@media (min-width: 1200px) {
	.first_div {
		width: calc(100% - 160px);
		margin-top: 96px;
		margin-bottom: 48px;
	}

	.first_div_desc {
		margin-left: 64px;
	}

	.stats {
		position: absolute;
		bottom: 40%;
		left: 10%;
		width: 50%;
		padding: 40px 45px;
	}

	.text {
		width: calc(100% - 160px);
		padding: 96px 96px 64px 96px;
	}

	.text_div2 {
		margin-left: 48px;
	}
}