/* Общий стиль для testimonials и его дочерних элементов */
.wrapper {
    background-color: rgba(251, 246, 234, 1);
    display: flex;
    flex-direction: row;
    height: 464px;
    padding: 75px 112px;
    margin: 0 80px;
}

.about {
    border-right: 1px solid rgba(109, 110, 118, 0.4);
    padding-right: 80px;
}

.about_h3 {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 3px;
    margin-bottom: 16px;
}

.about_h2 {
    font-size: 36px;
    line-height: 48px;
    letter-spacing: -2px;
    margin-bottom: 16px;
}

.testimonial_author {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.img {
    width: 48px;
    height: 48px;
}

.bottom_block {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.prev_button {
    margin-right: 24px;
    cursor: pointer;
}

.next_button {
    cursor: pointer;
}

.testimonial_author_name {
    font-size: 24px;
    line-height: 32px;
}

.testimonial_author_location {
    font-size: 16px;
    line-height: 28px;
    color: rgba(109, 110, 118, 1);
}

.testimonial_text {
    font-size: 24px;
    line-height: 32px;
}

.testimonial {
    padding: 0 0 0 96px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

/* Адаптация для экранов от 768px до 991.98px */
@media (min-width: 768px) and (max-width: 991.98px) {
    .wrapper {
        flex-direction: column;
        height: auto;
        padding: 40px;
        margin: 0 40px;
    }

    .about {
        border-right: none;
        padding-right: 0;
        margin-bottom: 32px;
        text-align: center;
    }

    .about_h2 {
        font-size: 28px;
        line-height: 36px;
    }

    .testimonial {
        padding: 0;
    }

    .bottom_block {
        flex-direction: column;
        align-items: center;
        margin-top: 16px;
    }

    .buttons {
        margin-top: 16px;
    }
}

/* Адаптация для экранов от 992px до 1199.98px */
@media (min-width: 992px) and (max-width: 1199.98px) {
    .wrapper {
        padding: 60px 96px;
        margin: 0 60px;
    }

    .about {
        padding-right: 60px;
    }

    .about_h2 {
        font-size: 32px;
        line-height: 42px;
    }

    .testimonial {
        padding: 0 0 0 72px;
    }
}

/* Адаптация для экранов от 1200px и выше */
@media (min-width: 1200px) {
    .wrapper {
        padding: 75px 112px;
        margin: 0 80px;
    }

    .about {
        padding-right: 80px;
    }

    .about_h2 {
        font-size: 36px;
        line-height: 48px;
    }

    .testimonial {
        padding: 0 0 0 96px;
    }
}  
