/* Общий стиль для Footer и его дочерних элементов */
.wrapper {
    height: 560px;
    background-color: rgba(35, 37, 54, 1);
    display: flex;
    flex-direction: column;
    padding: 64px 80px;
}

.nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 80px;
}

.nav_right {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-grow: 1;
    align-items: center;
}

.nav_cont_link {
    text-decoration: none;
    color: white;
    font-family: "Inter", serif;
    font-weight: 400;
    margin: 0 20px;
}

.form {
    height: 256px;
    background-color: #2e3040;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 80px;
    margin-bottom: 48px;
}

.form_left {
    color: white;
}

.form_right {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-grow: 1;
}

.input {
    width: 323px;
    background-color: transparent;
    border: 1px solid rgba(76, 76, 76, 1);
    margin-right: 32px;
    padding-left: 16px;
    color: white;
}

.input:user-invalid {
    border: 1px solid red;
}

.socials {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 144px;
    height: 17px;
}

.contacts_text {
    font-size: 16px;
    line-height: 28px;
    color: rgb(175, 175, 175);
}

.wrapper_socials {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-grow: 1;
}

.contacts {
    display: flex;
    flex-direction: row;
    align-items: center;
}

/* Адаптация для экранов от 768px до 991.98px */
@media (min-width: 768px) and (max-width: 991.98px) {
    .wrapper {
        padding: 40px;
    }

    .form {
        flex-direction: column;
        padding: 40px;
        height: auto;
    }

    .form_left {
        text-align: center;
        margin-bottom: 24px;
    }

    .form_right {
        justify-content: center;
    }

    .nav {
        flex-direction: column;
        margin-bottom: 40px;
    }

    .nav_right {
        justify-content: center;
    }
}

/* Адаптация для экранов от 992px до 1199.98px */
@media (min-width: 992px) and (max-width: 1199.98px) {
    .wrapper {
        padding: 60px;
    }

    .form {
        padding: 60px;
    }

    .nav {
        margin-bottom: 60px;
    }
}

/* Адаптация для экранов от 1200px и выше */
@media (min-width: 1200px) {
    .wrapper {
        padding: 64px 80px;
    }

    .form {
        padding: 80px;
    }

    .nav {
        margin-bottom: 80px;
    }
}
