.nav_cont {
    display: flex;
    flex-direction: row;
    background-color: rgba(35, 37, 54, 1);
    height: 80px;
    align-items: center;
    position: sticky;
    top: 0;
    width: 100%;
    z-index: 100;
}

.logo_img {
    width: auto;
    margin-left: 85px;
}

.nav_cont_link {
    text-decoration: none;
    color: white;
    font-family: "Inter", serif;
    font-weight: 400;
    margin: 0 20px 0 20px;
}

.nav_cont_right {
    display: flex;
    flex-direction: row;
    justify-content: right;
    flex-grow: 1;
    align-items: center;
    margin-right: 80px;
}

.nav_cont_button {
    font-family: "Sen", serif;
    font-weight: 700;
    font-size: 18px;
    background-color: white;
    color: rgba(35, 37, 54, 1);
    border: none;
    height: 55px;
    width: 180px;
    line-height: 24px;
    margin-left: 12px;
}

/* Adaptivity for screens from 768px to 992px */
@media (max-width: 992px) and (min-width: 768px) {
    .nav_cont {
        height: 70px;
    }
    .logo_img {
        margin-left: 50px;
    }
    .nav_cont_right {
        margin-right: 50px;
    }
    .nav_cont_link {
        margin: 0 15px;
        font-size: 14px;
    }
    .nav_cont_button {
        font-size: 16px;
        height: 50px;
        width: 160px;
    }
}

/* Adaptivity for screens from 992px to 1200px */
@media (max-width: 1200px) and (min-width: 992px) {
    .nav_cont {
        height: 75px;
    }
    .logo_img {
        margin-left: 60px;
    }
    .nav_cont_right {
        margin-right: 60px;
    }
    .nav_cont_link {
        margin: 0 18px;
        font-size: 15px;
    }
    .nav_cont_button {
        font-size: 17px;
        height: 53px;
        width: 170px;
    }
}

/* Adaptivity for screens larger than 1200px */
@media (min-width: 1200px) {
    .nav_cont {
        height: 80px;
    }
    .logo_img {
        margin-left: 85px;
    }
    .nav_cont_right {
        margin-right: 80px;
    }
    .nav_cont_link {
        margin: 0 20px;
        font-size: 16px;
    }
    .nav_cont_button {
        font-size: 18px;
        height: 55px;
        width: 180px;
    }
}
