.cat_buttons {
    display: flex;
    flex-wrap: wrap;
    justify-content: center; /* Центровка элементов по горизонтали */
    gap: 16px; /* Пространство между кнопками */
}

.wrapper_h2{
    font-size: 36px;
    line-height: 48px;
    letter-spacing: -2px;
}

.wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.wws {
    margin: 128px 80px 0 80px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.wws > div {
    flex: 1;
}

.wws img {
    max-width: 100%;
    height: auto;
}

.wws_text_block {
    height: 584px;
    background-color: white;
    position: relative;
    padding: 80px;
}

.wws_h3 {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 3px;
    margin-bottom: 24px;
}

.wws_h1 {
    font-size: 48px;
    line-height: 64px;
    letter-spacing: -2px;
    margin-bottom: 16px;
}

.wws_h4 {
    color: rgba(109, 110, 118, 1);
    font-size: 16px;
    line-height: 28px;
    margin-bottom: 32px;
}

/* Адаптация для экранов от 768px до 991.98px */
@media (min-width: 768px) and (max-width: 991.98px) {
    .wws {
        margin: 64px 40px 0 40px;
        flex-direction: column;
        align-items: center;
    }

    .wws > div {
        flex: none;
        width: 100%;
        margin-bottom: 32px;
    }

    .wws_text_block {
        height: auto;
        padding: 40px;
        text-align: center;
    }

    .wws_h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .wws_h4 {
        font-size: 14px;
        line-height: 24px;
    }
}

/* Адаптация для экранов от 992px до 1199.98px */
@media (min-width: 992px) and (max-width: 1199.98px) {
    .wws {
        margin: 96px 60px 0 60px;
    }

    .wws > div {
        flex: 1;
        margin-bottom: 0;
    }

    .wws_text_block {
        height: auto;
        padding: 60px;
    }

    .wws_h1 {
        font-size: 42px;
        line-height: 56px;
    }

    .wws_h4 {
        font-size: 15px;
        line-height: 26px;
    }
}

/* Адаптация для экранов от 1200px и выше */
@media (min-width: 1200px) {
    .wws {
        margin: 128px 80px 0 80px;
    }

    .wws > div {
        flex: 1;
    }

    .wws_text_block {
        height: 584px;
        padding: 80px;
    }

    .wws_h1 {
        font-size: 48px;
        line-height: 64px;
    }

    .wws_h4 {
        font-size: 16px;
        line-height: 28px;
    }
}
