/* Default Styles */
.wrapper{
    display: flex;
    flex-direction: row;
    margin: 128px 80px 0px 80px;
    justify-content: space-between;
    width: calc(100% - 160px);
}

.wrapper_wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.feat_post{
    border: 1px solid rgba(109, 110, 118, 0.1);
    padding: 32px;
}

.by{
    margin-top: 32px;
    font-size: 14px;
}

.by_author{
    color: rgba(89, 46, 169, 1);
}

.title{
    font-size: 28px;
    margin-top: 24px;
    letter-spacing: -1px;
    line-height: 40px;
}

.feat_post_section{
    width: 733px;
}

.desc{
    margin-top: 16px;
    font-size: 16px;
    margin-bottom: 32px;
}

.featpost_type{
    margin-bottom: 32px;
}

.all_posts_head{
    display: flex;
    flex-direction: row;
    margin-bottom: 32px;
}

.all_posts_link{
    display: flex;
    justify-content: right;
    flex-grow: 1;
    align-items: center;
    text-decoration: none;
    color: rgba(89, 46, 169, 1);
    font-size: 16px;
}

.all_posts_cont{
    display: flex;
    flex-direction: column;
    height: 700px;
}

.all_posts_post{
    padding: 16px;
    height: 175px;
    align-content: center;
}

.all_posts_post:hover{
    background-color: rgba(251, 246, 234, 1);
}

.all_posts{
    margin-left: 32px;
}

.all_posts_by{
    font-size: 14px;
    margin-bottom: 8px;
}

.all_posts_by_author{
    color: rgba(89, 46, 169, 1);
}

.all_posts_post_link{
    display: inline-block;
    text-decoration: none;
    color: black;
}

/* Media Queries */

/* For screens from 768px to 992px */
@media (max-width: 992px) and (min-width: 768px) {
    .wrapper{
        flex-direction: column;
        margin: 64px 40px;
    }

    .feat_post_section{
        width: 100%;
    }

    .feat_post img {
        width: 100%;
        height: auto;
        object-fit: cover; /* Сохраняет пропорции изображения */
        max-height: 350px; /* Ограничение высоты изображения */
    }

    .all_posts{
        margin-left: 0;
        margin-top: 32px;
    }

    .all_posts_cont{
        height: auto;
    }

    .all_posts_post{
        height: auto;
        padding: 16px;
    }

    .all_posts_post_link h2{
        font-size: 22px;
    }

    .title{
        font-size: 24px;
    }

    .desc{
        font-size: 14px;
    }

    .all_posts_link{
        font-size: 14px;
    }
}

/* For screens from 992px to 1200px */
@media (max-width: 1200px) and (min-width: 992px) {
    .wrapper{
        margin: 128px 60px;
        flex-direction: column;
    }

    .all_posts{
        margin-left: 0;
        margin-top: 32px;
    }

    .feat_post_section{
        width: 100%;
    }

    .title{
        font-size: 26px;
    }

    .desc{
        font-size: 15px;
    }

    .all_posts_post{
        height: 200px;
    }
}

/* For screens above 1200px */
@media (min-width: 1200px) {
    .wrapper{
        margin: 128px 80px;
    }

    .feat_post_section{
        width: 733px;
    }

    .title{
        font-size: 28px;
    }

    .desc{
        font-size: 16px;
    }

    .all_posts_post{
        height: 175px;
    }
}
