/* Основные стили */
.wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 128px 80px 0px 80px;
}

.colors{
    display: flex;
    flex-direction: row;
    width: 100%;
}

.white{
    width: 20%;
    height: 23px;
}

.yellow{
    background-color: rgba(255, 208, 80, 1);
    width: 60%;
    height: 23px;
}

.purple{
    background-color: rgba(89, 46, 169, 1);
    width: 20%;
    height: 23px;
}

.main{
    height: 500px;
    width: 100%;
    background-color: rgba(244, 240, 248, 1);
    display: flex;
    flex-direction: row;
}

.about_us{
    align-content: center;
    margin-left: 80px;
    flex: 0 0 calc(50% - 80px);
    margin-right: 40px;
}

.mission{
    align-content: center;
    flex: 0 0 calc(50% - 108px);
    margin-left: 40px;
    padding-right: 108px;
    margin-bottom: 26px;
}

.about_h3{
    font-size: 16px;
    letter-spacing: 3px;
    line-height: 20px;
    margin-bottom: 24px;
}

.about_h2{
    font-size: 36px;
    letter-spacing: -2px;
    line-height: 48px;
    margin-bottom: 16px;
}

.main_h4{
    font-size: 16px;
    line-height: 28px;
    color: rgba(109, 110, 118, 1);
    margin-bottom: 16px;
}

.about_link{
    font-size: 18px;
    line-height: 32px;
    text-decoration: none;
}

.mission_h3{
    font-size: 16px;
    letter-spacing: 3px;
    line-height: 20px;
    margin-bottom: 24px;
}

.mission_h2{
    font-size: 28px;
    letter-spacing: -1px;
    line-height: 40px;
    margin-bottom: 16px;
}

/* Адаптивные стили */

/* Для экранов от 768px до 992px */
@media (max-width: 992px) and (min-width: 768px) {
    .wrapper {
        margin: 64px 40px; /* Уменьшаем отступы */
    }

    .main {
        flex-direction: column;
        height: auto;
    }

    .about_us, .mission {
        margin-left: 0;
        margin-right: 0;
        flex: 0 0 100%;
        padding: 20px;
    }

    .about_h2 {
        font-size: 30px;
        line-height: 40px;
    }

    .mission_h2 {
        font-size: 24px;
        line-height: 36px;
    }

    .main_h4 {
        font-size: 14px;
        line-height: 24px;
    }

    .about_link {
        font-size: 16px;
        line-height: 28px;
    }
}

/* Для экранов от 992px до 1200px */
@media (max-width: 1200px) and (min-width: 992px) {
    .wrapper {
        margin: 128px 60px; /* Уменьшаем отступы */
    }

    .main {
        height: 500px;
        flex-direction: row;
    }

    .about_us {
        flex: 0 0 calc(50% - 60px);
        margin-right: 0;
    }

    .mission {
        flex: 0 0 calc(50% - 60px);
        padding-right: 60px;
    }

    .about_h2 {
        font-size: 32px;
        line-height: 44px;
    }

    .mission_h2 {
        font-size: 26px;
        line-height: 38px;
    }

    .main_h4 {
        font-size: 15px;
        line-height: 26px;
    }

    .about_link {
        font-size: 17px;
        line-height: 30px;
    }
}

/* Для экранов от 1200px */
@media (min-width: 1200px) {
    .wrapper {
        margin: 128px 80px;
    }

    .main {
        flex-direction: row;
    }

    .about_us {
        flex: 0 0 calc(50% - 80px);
    }

    .mission {
        flex: 0 0 calc(50% - 108px);
        padding-right: 108px;
    }

    .about_h2 {
        font-size: 36px;
        line-height: 48px;
    }

    .mission_h2 {
        font-size: 28px;
        line-height: 40px;
    }

    .main_h4 {
        font-size: 16px;
        line-height: 28px;
    }

    .about_link {
        font-size: 18px;
        line-height: 32px;
    }
}
