.wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 128px;
}

.header_wrapper{
    margin-bottom: 48px;
}

.header{
    font-size: 36px;
    line-height: 48px;
    letter-spacing: -2px;
}

.list{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.author{
    align-content: center;
    width: 296px;
    height: 324px;
    justify-items: center;
    background-color: rgba(244, 244, 244, 1);
    margin: 0 16px 32px 16px;
}

.author:hover{
    background-color: rgba(251, 246, 234, 1);
}

.image_section{
    width: 128px;
    height: 128px;
    border-radius: 50%;
    object-fit: cover;
    overflow: hidden;
}

.author_name{
    font-size: 28px;
    line-height: 40px;
    letter-spacing: -1px;
}

.author_role{
    font-size: 14px;
    line-height: 20px;
    color: rgba(109, 110, 118, 1);
    margin-bottom: 16px;
}

.socials{
    display: flex;
    flex-direction: row;
    width: 112px;
    justify-content: space-between;
}