.wrapper{
    height: 228px;
    width: 296px;
    border: 1px solid rgba(109, 110, 118, 0.3);
    padding: 32px 40px 32px 32px;
    display: inline-block;
    margin: 0 16px 32px 16px;
}

.wrapper:hover{
    background-color: rgba(255, 208, 80, 1);
}

.icon_background{
    background-color: rgba(251, 246, 234, 1);
    border: 1px solid transparent;
    border-radius: 5px 5px 5px 5px;
    height: 48px;
    width: 48px;
    margin-bottom: 16px;
    align-items: center;
    display: flex;
    justify-content: center;
}

.category{
    font-size: 28px;
    line-height: 40px;
    letter-spacing: -1px;
    margin-bottom: 16px;
}

.desc{
    font-size: 16px;
    line-height: 28px;
    color: rgba(109, 110, 118, 1);
}

/* Default styles apply for screens less than 768px */

/* Styles for screens from 768px to 992px */
@media (min-width: 768px) and (max-width: 992px) {
    .wrapper {
        width: 100%;
        height: auto;
        padding: 24px;
        margin: 16px 16px 0 16px;
        text-align: center;
    }

    .icon_background {
        margin: 0 auto 16px auto;
        height: 40px;
        width: 40px;
    }

    .category {
        font-size: 24px;
        line-height: 32px;
    }

    .desc {
        font-size: 14px;
        line-height: 24px;
    }
}

/* Styles for screens from 992px to 1200px */
@media (min-width: 992px) and (max-width: 1200px) {
    /* .wrapper {
        width: 240px;
        height: auto;
        padding: 28px;
        margin: 16px;
    } */

    .wrapper {
        width: 240px;
        height: auto;
        padding: 28px;
    }

    .icon_background {
        height: 44px;
        width: 44px;
    }

    .category {
        font-size: 26px;
        line-height: 36px;
    }

    .desc {
        font-size: 15px;
        line-height: 26px;
    }
}

/* Styles for screens wider than 1200px */
@media (min-width: 1200px) {
    .wrapper {
        width: 296px;
        height: 228px;
        padding: 32px 40px;
        margin: 16px;
    }

    .icon_background {
        height: 48px;
        width: 48px;
    }

    .category {
        font-size: 28px;
        line-height: 40px;
    }

    .desc {
        font-size: 16px;
        line-height: 28px;
    }
}
